// scripts/tooltip.js

const tooltips = document.querySelectorAll(".tooltip");


function updateTooltips() {
    for (let tooltip of tooltips) {

        if (!tooltip.querySelector("p")) {
            const p = document.createElement("p");
            p.textContent = tooltip.textContent;
            tooltip.textContent = "";
            tooltip.appendChild(p);
        }

        const button = tooltip.parentElement;

        positionTooltips();

        const dropdown = button.querySelector(".dropdown-content");

        button.addEventListener("pointerenter", (e) => {
            // Only show tooltip if entering the button itself
            if (e.target === button) {
                tooltip.classList.add("visible");
            }
        });

        button.addEventListener("pointerleave", (e) => {
            const to = e.relatedTarget;

            // Only hide tooltip if mouse leaves button and dropdown
            if (!button.contains(to) && !(dropdown && dropdown.contains(to))) {
                tooltip.classList.remove("visible");
            }
        });
    }
}

function positionTooltip(button, tooltip) {
    const btnRect = button.getBoundingClientRect();
    let toolRect = tooltip.getBoundingClientRect();

    tooltip.style.bottom = `${-toolRect.height - border}px`;
    tooltip.style.left = "none";

    toolRect = tooltip.getBoundingClientRect();

    if (toolRect.left < border || toolRect.right > window.innerWidth - border) {
        const rightPos = (window.innerWidth - btnRect.right) - (toolRect.width - btnRect.width) - border;
        const leftPos = border - btnRect.left;

        if (toolRect.right > window.innerWidth - border) {
            tooltip.style.left = `${rightPos}px`;
        } else if (toolRect.left < border) {
            tooltip.style.left = `${leftPos}px`;
        }
    }

    if (toolRect.bottom > window.innerHeight - border || toolRect.top < border) {
        tooltip.style.bottom = `${(btnRect.height - toolRect.height) / 2}px`;

        if (toolRect.bottom > window.innerHeight - border) {
            tooltip.style.left = `${btnRect.width + border}px`;
        }
    }

    toolRect = tooltip.getBoundingClientRect();

    const xdifference = (btnRect.x + btnRect.width / 2) - (toolRect.x);
    const ydifference = ((btnRect.y + btnRect.width / 2) - (toolRect.y));

    tooltip.style.transformOrigin = `${xdifference}px ${ydifference}px`;
}

function positionTooltips() {
    for (let tooltip of tooltips) {
        const button = tooltip.parentElement;
        positionTooltip(button, tooltip);
    }
}